html,
body {
  margin: 0;
  padding: 0;
}

#app {
  align-items: stretch;
  display: flex;
  min-height: 100vh;
  justify-content: stretch;
  touch-action: manipulation;
  width: 100%;
}

.app {
  background: linear-gradient(45deg, #888, #eee);
  flex: 1;
  font-family: sans-serif;
  font-size: 1.1rem;
  font-weight: 100;
  margin: 0;
  padding: 20px;
  text-align: center;
}

h1 {
  margin-top: 0;
}

.render-time {
  font-size: 0.6em;
}

.grid {
  display: grid;
  gap: 10px;
  grid-template-columns: repeat(4, 1fr);
  margin: 20px auto;
  max-width: 600px;
  user-select: none;
}

.tile-button {
  -webkit-tap-highlight-color: transparent;
  background: #eee;
  border: none;
  border-radius: 10px;
  box-shadow: #ddd 0 3px 0;
  cursor: pointer;
  font-family: sans-serif;
  font-size: 3.5em;
  font-weight: 900;
  line-height: 130px;
  padding-inline: 50px;
  text-align: center;
  text-transform: uppercase;
  transition: all 0.03s ease-in-out;
  &:hover {
    box-shadow: #ddd 0 8px 0;
    transform: translateY(-5px) scale(1.01);
  }
  &:active {
    box-shadow: #ddd 0 1px 0;
    transform: translateY(3px) scale(0.99);
  }
  &.selected {
    background: #ff8;
  }
  &.spent {
    opacity: 0.3;
  }
}

.grid-tile {
  font-size: 3em;
  line-height: 1;
  padding: 100% 0 0;
  position: relative;
  @media (min-width: 500px) {
    font-size: 3.5em;
  }
  @media (min-width: 600px) {
    font-size: 4em;
  }
}

.grid-tile-contents {
  align-items: center;
  display: flex;
  justify-content: center;
  position: absolute;
  inset: 0;
}

.grid-tile-count {
  font-size: 0.3em;
  line-height: 1;
  position: absolute;
  right: 5px;
  top: 5px;
}

.active-guess {
  max-width: calc(100vw - 40px);
  overflow: auto;
}

.guesses-wrapper {
  margin: 0 auto;
  max-width: 800px;
}

.meter-container {
  background: #eee;
  border: 2px solid #666;
  border-radius: 50px;
  display: block;
  margin: 10px auto;
  overflow: hidden;
  position: relative;
  width: 100%;
}

.meter-bar {
  position: absolute;
  bottom: 0;
  left: 0;
  top: 0;
  transition: 0.5s ease-in-out right;
}

.meter-text {
  font-size: 2em;
  font-weight: bold;
  inset: 0;
  position: absolute;
}

.sub-guesses-wrapper {
  justify-content: center;
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  gap: 10px;
}

.sub-guess {
  background: #222;
  border-radius: 10px;
  box-shadow: #0006 0 3px 3px;
  color: #fff;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  padding: 10px;
}

.sub-guess-length {
  font-size: 1.3em;
  font-weight: bold;
}

.sub-guess-guesses {
  margin-block: 10px;
}

.sub-guess-remaining {
  font-size: 1.3em;
  font-weight: bold;
}

.small-button {
  display: block;
  font-size: 1em;
  line-height: 50px;
  margin: 20px auto;
}

.win-message {
  animation: rotateAlternate calc(60s / 135) infinite alternate ease-in-out;
  color: red;
  display: inline-block;
  font-size: 2em;
}

@keyframes rotateAlternate {
  from {
    filter: hue-rotate(0deg);
    rotate: -10deg;
    scale: 1;
  }
  to {
    filter: hue-rotate(360deg);
    rotate: 10deg;
    scale: 1.2;
  }
}
