html, body {
  margin: 0;
  padding: 0;
}

#app {
  touch-action: manipulation;
  justify-content: stretch;
  align-items: stretch;
  width: 100%;
  min-height: 100vh;
  display: flex;
}

.app {
  text-align: center;
  background: linear-gradient(45deg, #888, #eee);
  flex: 1;
  margin: 0;
  padding: 20px;
  font-family: sans-serif;
  font-size: 1.1rem;
  font-weight: 100;
}

h1 {
  margin-top: 0;
}

.render-time {
  font-size: .6em;
}

.grid {
  user-select: none;
  grid-template-columns: repeat(4, 1fr);
  gap: 10px;
  max-width: 600px;
  margin: 20px auto;
  display: grid;
}

.tile-button {
  -webkit-tap-highlight-color: transparent;
  cursor: pointer;
  text-align: center;
  text-transform: uppercase;
  background: #eee;
  border: none;
  border-radius: 10px;
  padding-inline: 50px;
  font-family: sans-serif;
  font-size: 3.5em;
  font-weight: 900;
  line-height: 130px;
  transition: all 30ms ease-in-out;
  box-shadow: 0 3px #ddd;
}

.tile-button:hover {
  transform: translateY(-5px)scale(1.01);
  box-shadow: 0 8px #ddd;
}

.tile-button:active {
  transform: translateY(3px)scale(.99);
  box-shadow: 0 1px #ddd;
}

.tile-button.selected {
  background: #ff8;
}

.tile-button.spent {
  opacity: .3;
}

.grid-tile {
  padding: 100% 0 0;
  font-size: 3em;
  line-height: 1;
  position: relative;
}

@media (width >= 500px) {
  .grid-tile {
    font-size: 3.5em;
  }
}

@media (width >= 600px) {
  .grid-tile {
    font-size: 4em;
  }
}

.grid-tile-contents {
  justify-content: center;
  align-items: center;
  display: flex;
  position: absolute;
  inset: 0;
}

.grid-tile-count {
  font-size: .3em;
  line-height: 1;
  position: absolute;
  top: 5px;
  right: 5px;
}

.active-guess {
  max-width: calc(100vw - 40px);
  overflow: auto;
}

.guesses-wrapper {
  max-width: 800px;
  margin: 0 auto;
}

.meter-container {
  background: #eee;
  border: 2px solid #666;
  border-radius: 50px;
  width: 100%;
  margin: 10px auto;
  display: block;
  position: relative;
  overflow: hidden;
}

.meter-bar {
  transition: right .5s ease-in-out;
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
}

.meter-text {
  font-size: 2em;
  font-weight: bold;
  position: absolute;
  inset: 0;
}

.sub-guesses-wrapper {
  flex-flow: wrap;
  justify-content: center;
  gap: 10px;
  display: flex;
}

.sub-guess {
  color: #fff;
  background: #222;
  border-radius: 10px;
  flex-direction: column;
  justify-content: space-between;
  padding: 10px;
  display: flex;
  box-shadow: 0 3px 3px #0006;
}

.sub-guess-length {
  font-size: 1.3em;
  font-weight: bold;
}

.sub-guess-guesses {
  margin-block: 10px;
}

.sub-guess-remaining {
  font-size: 1.3em;
  font-weight: bold;
}

.small-button {
  margin: 20px auto;
  font-size: 1em;
  line-height: 50px;
  display: block;
}

.win-message {
  color: red;
  font-size: 2em;
  animation: .444444s ease-in-out infinite alternate rotateAlternate;
  display: inline-block;
}

@keyframes rotateAlternate {
  from {
    filter: hue-rotate();
    rotate: -10deg;
    scale: 1;
  }

  to {
    filter: hue-rotate(360deg);
    rotate: 10deg;
    scale: 1.2;
  }
}
